import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import ProjectCard from '../../components/ProjectCard';
import Projects from '../../assets/projects.json';
import CardsContext from '../../contexts/CardsContext';

const Works = () => {
  const [cardDisplay, setCardDisplay] = useState(true);
  const toggleCards = () => { 
    setCardDisplay(cardDisplay => !cardDisplay)
    console.log('toggle');
  };  
  return (
      <main className='Works'>
        <div className='hero'>
          <h2>Au fil des annes, nous accompagnons les meilleurs</h2>
          <p>Dcouvrez pas  pas comment nous sommes prsents pour lancer vos marques prfres</p>
        </div>
        <CardsContext.Provider value={ cardDisplay }>
          {(cardDisplay) && 
          <section className='projects'>
            <ProjectCard project={Projects['darwin']}/>
            <ProjectCard project={Projects['tropcstraw']}/>
            <ProjectCard project={Projects['kedal']}/>
          </section>
            || 
          <section className='projects'>
            <Link to={{ pathname: `/works/darwin-study-case` }}>Dcouvrir le projet Darwin</Link>
            <Link to={{ pathname: `/works/tropcstraw-study-case` }}>Dcouvrir le projet TropCStraw</Link>
            <Link to={{ pathname: `/works/kedal-study-case` }}>Dcouvrir le projet Kedal</Link>
          </section>
          }
        </CardsContext.Provider>

        <button onClick={toggleCards}>Changer affichage des projets</button>
      </main>
  )
};

export default Works;